*, *::before, *::after{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    margin: 0;
    padding: 0;
    background: #eee;
    font-family: sans-serif;
}


.bgImage{
    position: fixed;
    display: block;
    z-index: -1;
    filter: brightness(40%);
    height: 100%;
    width: 100%;
    object-fit: cover;
}


.navbar{
    /***** stay on top *****/
    /*position: sticky; */
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 5px 15px;
    background-image:  linear-gradient(to right, rgb(25, 25, 25), rgba(25, 25, 25, 0.3));
    z-index: 100;
}
.navbar p{
    color: #eee;
    font-size: 20px;
}

.navbarlinks{
    width: 30%;
    display: flex;
    justify-content: space-between;
}

.navitem{
    margin: auto;
    padding: 0;
    color: #eee;
    font-size: 22px;
}

.navitem p::after{
    content: "";
    display: block;
    height: 2px;
    width: 0px;
    background: rgb(238, 238, 238);
    transition: width ease-out 300ms;
}

.navitem p:hover::after{
    width: 100%;
}

.navitem p:hover{
    color: rgb(194, 194, 194);
    cursor: pointer;
}

.logoicon{
    height: 50px;
    width: 65px;
    padding: 5px 15px;
}

.logolink{
    height: 50px;
    width: 50px;
    padding: 5px 5px;
    margin: 0px 10px;
    border-radius: 30%;
    background-color: rgba(255, 255, 255, 0.25);
    color: rgb(0, 0, 200);
    transition: transform ease-out 300ms;
}

.logolink:hover{
    transform: scale(1.3);
}

/********** Add tooltip ***********/
[tooltip]{
    position: relative;
}

[tooltip]::after{
    position: absolute;
    margin: 0 auto;
    display: block;
    content: attr(tooltip);
    border-radius: 5px;
    padding: 5px;
    background: rgb(0, 0, 105, 0.6);
    width: fit-content;
    color: honeydew;
    
    bottom: -30px;
    left: -50%;

    white-space: nowrap; /*ser till att allt kommer p√• en rad*/
    /*animation*/
    transform: scale(0);
    transition: transform ease-out 400ms;
}
[tooltip]:hover::after{
    transform: scale(1);
}

.home{
    text-align: center;
    color: #eee;

    padding-top: 40vh;
    min-height: 100vh;
}

.home h3{
    font-size: 44px;
    text-shadow: 2px 2px #333;
}
.home h4{
    font-size: 26px;
    text-shadow: 2px 2px #333;
}

.projects{
    text-align: center;
    background-image: linear-gradient(to bottom, rgba(255, 253, 253, 0), #eee);
    min-height: 100vh;
    padding: 20vh 0;
    color: #ddd;
}

.project_card_content a{
    color: rgb(170, 170, 170);
    font-size: 1.4rem;
    text-decoration: none;
}

.project_info{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.project_info p{
    width: 100%;
}

.my_project{
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.project_info div{
    width: 250px;
}

.project_img_container{
    width: 300px;
    height: 250px;
    overflow: hidden;
    /*height: 300px;*/
    border-radius: 25px;
}
.project_img_container img{
    height: 250px;
}
.project_card_content{
    position: relative;
    height: 330px;
    width: 330px;
    margin: 20px;
    transition: transform 1s;
    transform-style: preserve-3d;
}
.project_card_front, .project_card_back{
    position: absolute;
    height: 330px;
    width: 330px;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    border-radius: 5%;
    background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.5), rgba(71, 71, 71, 0.5));
    color: rgb(202, 202, 202);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    backface-visibility: hidden;
}
.project_card_back{
    background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.5), rgba(71, 71, 71, 0.5));
    transform: rotateY(180deg);
    color: rgb(200, 200, 200);
}

.project_card:hover .project_card_content{
    transform: rotateY(180deg);
}

.project_links{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-between;
    bottom: 20px;
    color: #eee;
}
.project_links a{
    color: #eee;
    /*text-decoration: overline underline;*/
    padding: 5px;
    border-radius: 10px;
    border: 1px solid rgba(238, 238, 238, 0.6);
    transition: all 0.5s ease-in-out;
}
.project_links a:hover{
    color: #000;
}

.about{
    text-align: center;
    font-size: larger;
    margin: 0 auto;
    background: #eee;
    min-height: 100vh;
    padding: 35vh 0;
}
.about h2{
    text-align: center;
    text-decoration: underline;
}

.contact{
    text-align: center;
    padding: 2vh 0;
    background-image:  linear-gradient(to right, rgba(25, 25, 25, 0.5), rgba(25, 25, 25, 0.2));
}

@media screen and (max-width: 1100px){
    .my_project{
        flex-direction: column;
    }
    .project{
        width: 90vw;
    }
    .project a{
        font-size: 1rem;
    }
    .project div{
        width: 100%;
    }
    .project p{
        width: 100%;
    }

    /**** Navbar responsive ****/
    .navbarTitle{
        width: 0px;
        opacity: 0;
        pointer-events: none;
    }
    .navbarlinks{
        width: 100%;
    }
}

